import React from 'react';
import AppLayout from '/components/AppLayout/AppLayout'
import stylesheet from "styles/index.less";

class Home extends React.Component {
    render() {
        return(
            <AppLayout>
                <h1>Make the world better.</h1>
                <p>
                    We here at Ampersand Labs believe in a better world. Our planet has a lot of potential, and it is our mission to bring the best out of it and the people that habitate there. It is important to focus in various areas to make our impact on the world bigger and more notorious. 
                </p>
            </AppLayout>
        ) 
    }
}

export default Home


